import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Header from '../Header/Header';
import routes from '../../pages/asyncPagesList';

const App = () => {
  return (
    <div className="App">
      <Header />
      <Switch>
        {routes.map(route => (
          <Route key={route.path} {...route} />
        ))}

        <Redirect to="/" />
      </Switch>
    </div>
  );
};

export default App;
