import Loadable from 'react-loadable';
import Loader from '../components/Loader/Loader';

const AsyncHome = Loadable({
  loader: () =>
    import('./Home/HomeContainer' /* webpackChunkName: 'home-page' */),
  loading: Loader,
});

const AsyncUserList = Loadable({
  loader: () =>
    import('./UserList/UserListContainer' /* webpackChunkName: 'user-list' */),
  loading: Loader,
});
const AsyncFilmPage = Loadable({
  loader: () =>
    import('./FilmPage/FilmPageContainer' /* webpackChunkName: 'film-page' */),
  loading: Loader,
});

const routes = [
  {
    exact: true,
    path: '/',
    component: AsyncHome,
  },
  {
    exact: false,
    path: '/list',
    component: AsyncUserList,
  },
  {
    exact: false,
    path: '/:title/:id',
    component: AsyncFilmPage,
  },
];
export default routes;
